import React, { useState } from "react";
import firebase from "gatsby-plugin-firebase"
import ReactMarkdown from "react-markdown";

import SortableTree from 'react-sortable-tree'; //, { addNodeUnderParent, getNodeKey }
import 'react-sortable-tree/style.css'; // This only needs to be imported once in your app

import Header from '../components/header';

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

const isBrowser = typeof window !== `undefined`;

export default () => {
    const [selectedMarkdown, setSelectedMarkdown] = useState('-M4J5rCVM4qSz4W3zpHJ');

    if (!isBrowser) return null


    return <>
        <Header siteTitle={"Econolabs"} />

        <Row>
            <Col sm="6"><TableOfContents /></Col>
            <Col sm="6">
                <Container>
                    <ShowParagraph selectedMarkdown={selectedMarkdown} />
                </Container>
            </Col>
        </Row>

    </>
}


class TableOfContents extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            treeData: [
                { title: 'Chicken', children: [{ title: 'Egg' }] },
                { title: 'Fish', children: [{ title: 'fingerline' }] },
                { title: 'New' },
                {
                    title: "Disable dragging on a per-node basis with the `canDrag` prop",
                    subtitle: "Or set it to false to disable all dragging.",
                },
                {
                    title: "You cannot give this children",
                    subtitle:
                        "Dropping is prevented via the `canDrop` API using `nextParent`",
                },
                {
                    title: "When node contents are really long"
                }
            ],
        };
    }

    render() {
        return (
            <div style={{ height: 400 }}>
                <SortableTree
                    treeData={this.state.treeData}
                    onChange={treeData => this.setState({ treeData })}


                />
            </div>
        );
    }
}


function ShowParagraph(props) {
    const [data, setData] = React.useState(null)

    React.useEffect(() => {
        firebase
            .database()
            .ref("/media")
            .child(`${props.selectedMarkdown}`)
            .once("value")
            .then(snapshot => {
                setData(snapshot.val())
            })
    }, [props.selectedMarkdown])

    console.log(data);

    return <div>{!!data?.meta ? <ReactMarkdown source={data?.meta} escapeHtml={false} /> : "..."}</div>
}